    width: 100%; 
    margin-top: 1rem; 
}

#slider {
    display: flex; 
    flex-direction: row; 
    align-items: center; 
    overflow: hidden; 
}

#slider figure {
    display: flex; 
    flex-direction: row; 
    align-items: center; 
    position: relative; 
    width: 100%; 
    margin:0;  
    left: 0; 
    -webkit-animation: 25s slider infinite; 
            animation: 25s slider infinite; 
}
.full-screen-image {
    display: none; 
}
.full-nav {
    display: none; 
}
.full-screen-para {
    display: none; 
}
.nav-main-header {
    display:none;
}
.full-screen-design {
    display: none; 
}
.splash-statement {
    display: none; 
}
.splash-full-btn {
    display: none;
}

@-webkit-keyframes slider {
    0% {
        left: 0; 
    }
    20% {
        left: 0; 
    }
    25% {
        left: -100%; 
    }
    45% {
        left: -100%; 
    }
    50% {
        left: -200%; 
    }
    70% {
        left: -200%; 
    }
    75% {
        left: -300%; 
    }
    85% {
        left: -300%
    }
    95% {
        left: -400%
    }
    100% {
        left: -400%;
    }
}

@keyframes slider {
    0% {
        left: 0; 
    }
    20% {
        left: 0; 
    }
    25% {
        left: -100%; 
    }
    45% {
        left: -100%; 
    }
    50% {
        left: -200%; 
    }
    70% {
        left: -200%; 
    }
    75% {
        left: -300%; 
    }
    85% {
        left: -300%
    }
    95% {
        left: -400%
    }
    100% {
        left: -400%;
    }
}

